import React, { useState, useContext, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { AuthContext } from '../helpers/AuthContext'
import axios from 'axios'
import VisibilityOffOutlinedIcon from '@mui/icons-material/VisibilityOffOutlined'
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined'


function Login() {

    const navigate = useNavigate()

    useEffect(() => {
        if (localStorage.getItem("accessToken")) {
            navigate('/')
        }
    }, [])

    const [type, setType] = useState('password')
    const { setAuthState } = useContext(AuthContext)
    const [signupMessage, setSignupMessage] = useState("")
    const [messageStyle, setMessageStyle] = useState("")
    const [userLogin, setUserLogin] = useState({
        email: "",
        password: ""
    })

    const handleChange = (e) => {
        setUserLogin(prev => ({
            ...prev,
            [e.target.name]: e.target.value
        }))
    }

    const handleSubmit = async (e) => {
        e.preventDefault()

        try {
            axios.post("https://proud-colt-jumper.cyclic.app/api/v1/users/login", userLogin).then((response) => {
                if (!response.data.success) {
                    setSignupMessage(response.data.message)
                    setMessageStyle("alert alert-danger")
                } else {
                    localStorage.setItem("accessToken", response.data.token)
                    setAuthState({
                        email: response.data.email,
                        id: response.data.id,
                        status: true
                    })
                    setSignupMessage(response.data.message)
                    setMessageStyle("alert alert-success")
                    window.location.replace('/')
                }
            })
        } catch (error) {
            console.log(error)
        }
    }

    const handleToggle = () => {
        if (type === 'password') {
            setType('text')
        } else {
            setType('password')
        }
    }

    return (
        <div className='container pt-5'>
            <div className='formContainer shadow'>
                <h1>Login</h1>
                <br />
                <form onSubmit={handleSubmit}>
                    <div className={messageStyle} role="alert">
                        {signupMessage}
                    </div>         
                    <label>Email</label>
                    <div className='inputField mb-3'>
                        <input type="email" name='email' onChange={handleChange} maxLength='32' placeholder='Enter your email address...' required/>
                    </div>
                    <label>Password</label>
                    <div className='inputField mb-3'>
                        <input type={type} name='password' onChange={handleChange} maxLength='32' placeholder='Enter your password...' required/>
                        <span onClick={handleToggle}>{type === 'password' ? <VisibilityOffOutlinedIcon /> : <VisibilityOutlinedIcon />}</span>
                    </div>
                    <div className='d-grid mt-5 mb-3'>
                        <button type="submit" className="btn btn-primary btn-lg btn-block">Login</button>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default Login