import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import axios from 'axios'
import CheckCircleIcon from '@mui/icons-material/CheckCircle';

function Products() {

    const navigate = useNavigate()
    const [checkMessage, setCheckMessage] = useState(false)
    const [addProductMessage, setAddProductMessage] = useState("")
    const [searchProduct, setSearchProduct] = useState("")
    const [messageStyle, setMessageStyle] = useState("")
    const [listOfProducts, setListOfProducts] = useState([])
    const [addProduct, setAddProduct] = useState({
        name: "",
        codename: ""
    })
    const [refreshKey, setRefreshKey] = useState(0)


    useEffect(() => {
        if (!localStorage.getItem("accessToken")) {
            navigate('/')
        }
    }, [])

    useEffect(() => {
        axios.get("https://proud-colt-jumper.cyclic.app/api/v1/products", {
            headers: { accessToken: localStorage.getItem('accessToken') }
        })
            .then((response) => {
                setListOfProducts(response.data.productList)
            })
    }, [refreshKey])


    const onChangeAdd = (e) => {
        setAddProduct(prev => ({
            ...prev,
            [e.target.name]: e.target.value         
        }))
    }

    const handleSubmit = async (e) => {
        e.preventDefault()

        try {
            await axios.post("https://proud-colt-jumper.cyclic.app/api/v1/products", addProduct, {
                headers: { accessToken: localStorage.getItem('accessToken') }
            }).then((response) => {
                if (!response.data.success) {
                    setAddProductMessage(response.data.message)
                    setMessageStyle("alert alert-danger")
                } else {
                    setRefreshKey(oldKey => oldKey + 1)
                    setAddProductMessage(response.data.message)
                    setMessageStyle("alert alert-success")
                    setCheckMessage(true)
                    setAddProduct({
                        name: "",
                        codename: ""
                    })
                    
                    setInterval(() => {
                        setAddProductMessage("")
                        setMessageStyle("")
                        setCheckMessage(false)
                    }, 2500)
                    
                }
            })

        } catch (error) {
            console.log(error)
        }
        setRefreshKey(oldKey => oldKey + 1)
        e.target.name.focus();
    }

    const handleSearch = (e) => {
        e.preventDefault()

        axios.get(`https://proud-colt-jumper.cyclic.app/api/v1/products/${searchProduct}`, {
            headers: { accessToken: localStorage.getItem('accessToken') }
        }).then((response) => {
            if(response.data.productList.length === 0) {
                alert('No product found')
            } else {
                setListOfProducts(response.data.productList)
            }
        })
        setSearchProduct("")
    }

    const deleteProduct = (id, name) => {
        axios.delete(`https://proud-colt-jumper.cyclic.app/api/v1/products/${id}`, {
            headers: { accessToken: localStorage.getItem('accessToken') }
        }).then(() => {
            setRefreshKey(oldKey => oldKey + 1)
            alert(name + " has been deleted.")
            setListOfProducts(listOfProducts.filter((val) => {
                return val.id != id
            }))
        })
        setRefreshKey(oldKey => oldKey + 1)
    }

    return (
        <div className='container pt-5 pb-5'>
            <h1>Products</h1>
            <hr />
            <h3>Add New Products</h3>
            <form className="row g-3" onSubmit={handleSubmit}>
                <div className={messageStyle} role="alert">
                    {addProductMessage}
                    {checkMessage === true ? <CheckCircleIcon /> : null}
                </div>
                <div className="col-auto">
                    <label htmlFor="name">Product Name</label>
                    <input type="text" className="form-control" name='name' onChange={onChangeAdd} id="name" placeholder="Product name..." autoComplete='off' value={addProduct.name} required />
                </div>
                <div className="col-auto">
                    <label htmlFor="codename">Code Name</label>
                    <input type="text" className="form-control" name='codename' onChange={onChangeAdd} id="codename" placeholder="Product code name..." autoComplete='off' value={addProduct.codename} required />
                </div>
                <div className="col-auto">
                    <button type="submit" className="btn btn-primary mt-4">Add Product</button>
                </div>
            </form>

            <br /><hr /><br />
            <div className="container-fluid p-0 mt-3 mb-3">
                <form className="row" onSubmit={handleSearch}>
                    <div className='col-md-6 d-flex'>
                        <h3>Product List</h3>
                    </div>
                    
                    <div className="col-md-6 d-flex">
                        <input
                            className="form-control me-2"
                            type="search"
                            name="searchName"
                            onChange={(e) => { setSearchProduct(e.target.value) }}
                            placeholder="Search product.."
                            aria-label="Search"
                            value={searchProduct}
                        />
                        <button className="btn btn-primary" type="submit">Search</button>
                    </div>
                </form>
            </div>

            {listOfProducts.length === 0 ? <h3 className='mt-5'>No products found.</h3> : (
                <div className='table-responsive'>
                    <table className="table table-success table-striped">
                        <thead>
                            <tr>
                                <th scope="col">#</th>
                                <th scope="col">Name</th>
                                <th scope="col">Codename</th>
                                <th scope="col">Action</th>
                            </tr>
                        </thead>
                        <tbody>

                            {listOfProducts.map((product, key) => {
                                return (
                                    <tr key={key}>
                                        <th scope="row">{key + 1}</th>
                                        <td>{product.name}</td>
                                        <td>{product.codename}</td>
                                        <td>
                                            <button
                                                className='btn btn-danger btn-sm'
                                                onClick={() => {
                                                    deleteProduct(product.id, product.name)
                                                }}
                                            >Delete</button>
                                        </td>
                                    </tr>
                                )
                            })}
                        </tbody>
                    </table>
                </div>
            )}


        </div>
    )
}

export default Products