import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import axios from 'axios'
import VisibilityOffOutlinedIcon from '@mui/icons-material/VisibilityOffOutlined'
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined'

function Register() {

    const navigate = useNavigate()

    useEffect(() => {
        if (localStorage.getItem("accessToken")) {
            navigate('/')
        }
    }, [])

    const [type, setType] = useState('password')
    const [signupMessage, setSignupMessage] = useState("")
    const [messageStyle, setMessageStyle] = useState("")
    const [userAccount, setUserAccount] = useState({
        name: "",
        email: "",
        password: ""
    })

    const handleChange = (e) => {
        setUserAccount(prev => ({
            ...prev,
            [e.target.name]: e.target.value
        }))
    }
    
    const handleSubmit = async (e) => {
        e.preventDefault()

        try {
            const res = await axios.post("https://proud-colt-jumper.cyclic.app/api/v1/users", userAccount)
            const data = await res.data
            if (data.success === false) {
                setSignupMessage(data.message)
                setMessageStyle("alert alert-danger")
            } else {
                setSignupMessage(data.message)
                setMessageStyle("alert alert-success")
                setTimeout(() => navigate('/login'), 2000)
            }
        } catch (error) {
            console.log(error)
        }
    }

    const handleToggle = () => {
        if (type === 'password') {
            setType('text')
        } else {
            setType('password')
        }
    }

    return (
        <div className='container pt-5'>
            <div className='formContainer shadow'>
                <h1>Register</h1>
                <br />
                <form onSubmit={handleSubmit}>
                    <div className={messageStyle} role="alert">
                        {signupMessage}
                    </div>
                    <label>Name</label>
                    <div className='inputField mb-3'>
                        <input type="text" name='name' onChange={handleChange} maxLength='32' placeholder='Enter your name...' autoComplete='off' required/>
                    </div>
                    <label>Email</label>
                    <div className='inputField mb-3'>
                        <input type="email" name='email' onChange={handleChange} maxLength='32' placeholder='Enter your email address...' required/>
                    </div>
                    <label>Password</label>
                    <div className='inputField mb-3'>
                        <input type={type} name='password' onChange={handleChange} maxLength='32' placeholder='Enter your password...' required/>
                        <span onClick={handleToggle}>{type === 'password' ? <VisibilityOffOutlinedIcon /> : <VisibilityOutlinedIcon />}</span>
                    </div>
                    <div className='d-grid mt-5 mb-3'>
                        <button type="submit" className="btn btn-primary btn-lg btn-block">Register</button>
                    </div>   
                </form> 
            </div>
        </div>
    )
}

export default Register