import React, { useEffect } from 'react'
import { useNavigate } from 'react-router-dom'

function ViewOrders() {

    const navigate = useNavigate()

    useEffect(() => {
        if (!localStorage.getItem("accessToken")) {
            navigate('/')
        }
    }, [])

  return (
    <div className='container pt-5'>
            <div className='formContainer shadow'>
                <h1>View Orders</h1>
                <br />
                <form>
                   
                    <label>Name</label>
                    <div className='inputField mb-3'>
                        <input type="text" name='name' maxLength='32' placeholder='Enter your name...' required/>
                    </div>
                    <label>Email</label>
                    <div className='inputField mb-3'>
                        <input type="email" name='email' maxLength='32' placeholder='Enter your email address...' required/>
                    </div>
                    <div className='d-grid mt-5 mb-3'>
                        <button type="submit" className="btn btn-primary btn-lg btn-block">Add</button>
                    </div>   
                </form> 
            </div>
        </div>
  )
}

export default ViewOrders