import React, { useState, useContext, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { AuthContext } from '../helpers/AuthContext'
import axios from 'axios'
import VisibilityOffOutlinedIcon from '@mui/icons-material/VisibilityOffOutlined'
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined'

function ChangePass() {

    const navigate = useNavigate()

    useEffect(() => {
        if (!localStorage.getItem("accessToken")) {
            navigate('/')
        }
    }, [])

    const [type, setType] = useState('password')
    const [changePassMessage, setChangePassMessage] = useState("")
    const [messageStyle, setMessageStyle] = useState("")
    const [changePassInput, setChangePassInput] = useState({
        oldPass: "",
        newPass: "",
        confirmNewPass: ""
    })

    const handleChange = (e) => {
        setChangePassInput(prev => ({
            ...prev,
            [e.target.name]: e.target.value
        }))
    }

    const handleSubmit = (e) => {
        e.preventDefault()

        axios.put("https://proud-colt-jumper.cyclic.app/api/v1/users/changepassword", changePassInput, {
            headers: { accessToken: localStorage.getItem('accessToken')}
        }).then((response) => {
            if (!response.data.success) {
                setChangePassMessage(response.data.message)
                setMessageStyle("alert alert-danger")
            } else {
                setChangePassMessage(response.data.message)
                setMessageStyle("alert alert-success")
            }
        })
    }

    const handleToggle = () => {
        if (type === 'password') {
            setType('text')
        } else {
            setType('password')
        }
    }

    return (
        <div className='container pt-5'>
            <div className='formContainer shadow'>
                <h1>Change Password</h1>
                <hr />
                <form onSubmit={handleSubmit}>
                    <div className={messageStyle} role="alert">
                        {changePassMessage}
                    </div>
                    <div className='eyeToggle'>Old Password<span onClick={handleToggle}>{type === 'password' ? <VisibilityOffOutlinedIcon /> : <VisibilityOutlinedIcon />}</span></div>
                    <div className='inputField mb-3'>
                        <input type={type} name='oldPass' onChange={handleChange} maxLength='32' placeholder='Enter your old password...' required/>
                    </div>
                    <div>New Password</div>
                    <div className='inputField mb-3'>
                        <input type={type} name='newPass' onChange={handleChange} maxLength='32' placeholder='Enter your new password...' required/>
                    </div>
                    <div>Confirm New Password</div>
                    <div className='inputField mb-3'>
                        <input type={type} name='confirmNewPass' onChange={handleChange} maxLength='32' placeholder='Confirm your new password...' required/>
                    </div>
                    {/* <div className="form-floating mb-3">
                        <input type="password" className="form-control" name='oldPass' onChange={handleChange} id='oldPass' placeholder="Old password.." autoComplete='off' required />
                        <label htmlFor="oldPass">Old Password</label>
                    </div>
                    <div className="form-floating mb-3">
                        <input type="password" className="form-control" name='newPass' onChange={handleChange} id='newPass' placeholder="New password" autoComplete='off' required />
                        <label htmlFor="newPass">New Password</label>
                    </div>
                    <div className="form-floating mb-3">
                        <input type="password" className="form-control" name='confirmNewPass' onChange={handleChange} id='confirmNewPass' placeholder="Confirm new password" autoComplete='off' required />
                        <label htmlFor="confirmNewPass">Confirm New Password</label>
                    </div> */}
                    <div className='d-grid mt-5 mb-3'>
                        <button type="submit" className="btn btn-primary btn-lg btn-block">Submit</button>
                    </div>

                </form>
            </div>

        </div>
    )
}

export default ChangePass